.main-container-circle {
    height: 550px;
    width: calc(80% - 1px);
    margin: auto;
}

.chart-index-container {
    display: flex;
    margin-left: 140px;
    margin-right: 20px;
    justify-content: space-evenly;
    column-gap: 19px;
}

.chart-index {
    margin-bottom: 60px;
}

.chart-data-plot-container {
    list-style-type: none;
    padding-left: 5px;
    display: flex;
    flex-direction: column;
    height: 700px;
    overflow: auto;
}

.chart-data-plot {
    display: flex;
    align-items: center;
}

.chart-left-index {
    width: 140px;
    font-weight: 500;
}

.chart-plots-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    padding-left: 0px;
    width: 100%;
    column-gap: 19px;
}

.chart-plots {
    width: 90px;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    cursor: pointer;
}

.chart-plots-multi{
    column-gap: 7px;
}

.chart-val {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 12px;
}

.legend{
    display:flex;
    align-items: center;
    column-gap: 10px;
    margin-top: 30px;
}

.legend-color{
    border-radius:50%;
    width: 15px;
    height: 15px;
    border: solid 1px silver;
}

.legend-item{
    display: flex;
    align-items: center;
    column-gap: 5px;
}

@media (max-width: 1249px) {

    .main-container-circle {
        width: calc(95% - 1px);
    }

    .chart-index-container {
        display: flex;
        margin-left: 85px;
        justify-content: space-evenly;
        column-gap: 19px;
        width: calc(83% - 1px);
    }
    
    .chart-index{
        font-size: 10px;
        font-weight: 500;
    }

    .chart-left-index {
        width: 90px;
        font-size: 9px;
        font-weight: 500;
    }
}

@media (max-width: 768px) { 

    .chart-index{
        font-size: 10px;
        font-weight: 500;
    }

    .chart-plots-container{
        width: calc(83% - 1px);
    }

    .chart-plots {
        width: 60px;
        height: 60px;     
    }
}

/* width */
::-webkit-scrollbar {
    width: 7px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 999px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 999px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }